

namespace DataHistory {

    export type Manger = {
        cur: number;
        list: string[];
    }

    export const getInitialData = () => {
        return {
            cur: -1,
            list: []
        };
    }

    export const backup = (manager: Manger, data: string) => {
        if (manager.cur === -1) {
            manager.list.push(data);
        } else {
            // manager.list.splice(cur,);
            manager.cur = -1;
        }
    }

    export const undo = (manager: Manger): string => {
        if (manager.cur === -1) {
            manager.cur = manager.list.length - 1;
        } else {
            if(manager.cur > 0) {
                manager.cur --;
            }
        }
        return manager.list[manager.cur];
    }
}

export default DataHistory;
